nav {
  background: rgba(0, 0, 0, 0.7); 
  color: var(--color-white);
  width: max-content;
  display: block;
  padding: 0.5rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 1rem;
  display: flex;
  gap: 0.5rem;
  border-radius: 2rem;
  backdrop-filter: blur(15px);
}

nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-white);
  font-size: 1.1rem;
}

nav a:hover {
  background: rgba(255, 255, 255, 0.15); /* Slight white overlay on hover */
  color: var(--color-bg);
}

nav a.active {
  background: var(--color-bg);
  color: var(--color-black);
}