.container.contact__container {
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    gap: 12%;
}

.contact__option {
    background: var(--color-bg);  /* Lighter background */
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 2px solid var(--color-black);
    transition: var(--transition);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    color: var(--color-black);  /* Dark text color */
}

.contact__option:hover {
    background: var(--color-light-black);  /* Slightly darker background on hover */
    border-color: var(--color-black);
    transform: translateY(-3px);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
    color: var(--color-bg);  /* Lighter text color on hover */
}

.contact__option-icon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
    color: inherit;  /* Inherits the text color of the parent */
}

.contact__option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
    color: inherit;  /* Inherits the text color of the parent */
    transition: color 0.3s;
}

.contact__option a:hover {
    color: var(--color-primary-variant);
}

.contact__option h5, .contact__option h4 {
    color: inherit;  /* Inherits the text color of the parent */
}


form {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

input,
textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: var(--color-bg);
    border: 2px solid var(--color-black);  /* Altered for the B/W theme */
    resize: none;
    color: var(--color-black);  /* Altered for the B/W theme */
}

@media screen and (max-width: 1024px) {
    .container.contact__container {
        grid-template-columns: 1fr;
        gap: 2rem;
    }
}

@media screen and (max-width: 600px) {
    .container.contact__container {
        width: var(--container-width-sm);
    }
}
